import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';


const routes: Routes = [
  {
    path: "dashboard",
    loadChildren: () => import("./desktop/dashboard/dashboard.module").then(m => m.DashboardModule)
  },
  {
    path: 'cafe-pos',
    loadChildren: () => import('./desktop/cafe-pos/cafe-pos.module').then(m => m.CafePosModule)
  },
  {
    path: 'cafe-desktop',
    loadChildren: () => import('./desktop/cafe-desktop/cafe-desktop.module').then(m => m.CafeDesktopModule)
  },
  {
    path: 'cafe-fnb',
    loadChildren: () => import('./desktop/cafe-fnb/cafe-fnb.module').then(m => m.CafeFnbModule)
  },
  {
    path: 'manage-key',
    loadChildren: ()=> import('./desktop/menu/developers/manage-keys/manage-keys.module').then(m=>m.ManageKeysModule)
  },
  {
    path: 'coordination',
    loadChildren: () => import('./desktop/coordination/coordination.module').then(m => m.CoordinationModule)
  },
  {
    path: 'payment',
    loadChildren: () => import('./desktop/payment/payment.module').then(m => m.PaymentModule)
  },
  {
    path:'exchange-voucher',
    loadChildren: () => import('./desktop/exchange-voucher/exchange-voucher.module').then(m => m.ExchangeVoucherModule)
  },
  {
    path:'prepaid-card',
    loadChildren: () => import('./desktop/prepaid-card/prepaid-card.module').then(m => m.PrepaidCardModule)
  },

  {
    path: 'sales-voucher',
    loadChildren: () => import('./desktop/sales-voucher/sales-voucher.module').then(m => m.SalesVoucherModule)
  },
  {
    path: 'sales-payment-card',
    loadChildren: () => import('./desktop/sales-payment-card/sales-payment-card.module').then(m => m.SalesPaymentCardModule)
  },
  {
    path: 'bill-web-app',
    loadChildren: () => import('./bill-web-app/bill-web-app.module').then(m => m.BillWebAppModule)
  },
];
@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]
})
export class CafeModule { }
